import { request } from '../../request.js'
export default {
	data() {
		return {
			navList: {
				currentIndex: 0,
				list: [
					{
						name: '综合'
					},
					{
						name: '人气'
					},
					{
						name: '销量'
					},
					{
						name: '距离'
					},
					{
						name: '价格'
					},
				]
			},
			// 类别
			sort: 0,
			// 页码
			page: 1,
			latitude: '',
			longitude: '',
			// 储存商品数据
			goodsList: [],
			flag: true,
			$src: this.$src
		};
	},
	onLoad() {
		// 默认请求综合类
		this.getGoodsList(this.sort,this.page);
		// 读取经纬度
		uni.getStorage({
			key:'position',
			success: res => {
				this.latitude = res.data[0],
				this.longitude = res.data[1]
			}
		})
	},
	// 上拉加载更多
	onReachBottom() {
		this.page += 1;
		if(this.flag) {
			this.getGoodsList(this.sort,this.page,this.latitude,this.longitude);
		}
	},
	methods: {
		// tab切换
		changeNav(index) {
			this.navList.currentIndex = index;
			if(this.sort == index) {
				return
			}
			else {
				this.sort = index;
				this.goodsList = [];
				this.getGoodsList(this.sort,this.page,this.latitude,this.longitude)
			}
		},
		// index.php/wap/index/apiRecommendGoods
		// 参数：
		// sort（排序，0综合，1人气，2销量，3距离，4价格）
		// side(序向，0倒序，1正序）
		// latitude（纬度）
		// longitude（经度）
		// page（页码);
		// 获取数据
		getGoodsList(sort,page,latitude='',longitude='') {
			uni.showToast({
				title: '数据加载中',
				icon: 'loading',
				mask: true
			})
			request({
				url: 'index.php/wap/index/apiRecommendGoods',
				method: 'post',
				data: {
					sort: sort,
					page: this.page,
					latitude: this.latitude,
					longitude: this.longitude
				}
				
			}).then(res => {
				// 如果返回的条数小于10，默认不再进行上拉加载更多
				if(res.data.list.length < 10) {
					this.flag = false
				}
				uni.hideToast()
				// 图片处理
				res.data.list.forEach(item => {
					return item.pic_cover_small = this.$api + item.pic_cover_small
				})
				this.goodsList = [...this.goodsList,...res.data.list];
			})
		},
		// 查看商品详情
		goodsDetail(id) {
			uni.navigateTo({
				url:"../../common/goods-detail/goods-detail?id="+id
			})
		}
	}
}
